import React, { Component } from "react";
import axios from "axios";
export class Home extends Component {
  state = {
    file: null,
  };
  handleChange = (e) => {
    this.setState({
      [e.target.id]: e.target.value,
    });
  };
  handleCsvChange = (e) => {
    this.setState({
      file: e.target.files[0],
    });
  };
  handleJsonChange = (e) => {
    this.setState({
      file: e.target.files[0],
    });
  };
  handleCsvSubmit = (e) => {
    e.preventDefault();
    console.log(this.state);
    let form_data = new FormData();
    form_data.append("file", this.state.file, this.state.file.name);
    let url = "http://localhost:4000/uploadcsv/";
    axios
      .post(url, form_data, {
        headers: {
          "content-type": "application/json",
        },
      })
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  };
  handleJsonSubmit = (e) => {
    e.preventDefault();
    console.log(this.state);
    let form_data = new FormData();
    form_data.append("file", (this.state.file), this.state.file.name);
    let url = "http://localhost:4000/uploadjson/";
    axios({
      method: 'post',
      url: url,
      data: form_data,
      header: {
                'Accept': 'application/json',
                'Content-Type': 'multipart/form-data',
              },
        })
      .then((res) => {
        console.log(res.data);
      })
      .catch((err) => console.log(err));
  };
  render() {
    return (
      <div className="App">
        <form onSubmit={this.handleCsvSubmit}>
          <p>Please upload a .csv file here for adding multiple patients<br/>
            <input
              type="file"
              id="image"
              accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
              onChange={this.handleCsvChange}
              required
            />
          </p>
          <input type="submit" />
        </form>
        <br/>
        <br/>
        <br/>
        <form onSubmit={this.handleJsonSubmit}>
          <p>Please upload a .json file here for adding a single patient<br/>
            <input
              type="file"
              id="image"
              accept="application/JSON"
              onChange={this.handleJsonChange}
              required
            />
          </p>
          <input type="submit" />
        </form>
      </div>
    );
  }
}
